@tailwind base;
@tailwind components;
@tailwind utilities;

$header-height: 220px;

$color-bg: #edebf6;
$color-bg-light: white;
$color-light-text: rgb(111, 111, 111);
$color-text-base: #231C23;
$base-padding: 16px;
$half-padding: calc($base-padding / 2);

$search-bar-height: 64px;
$footer-height: 64px;

$side-container-size: 64px;
$main-container-size: 100%;
$main-container-margin-top:calc(2.5em + 10px);

$center-container-width: 80%;

$footer-margin:.5vw;
$footer-font-size:1em;

$mexinput-border-width:3px;
$queryblock-height:250px;

body {
  background-color: $color-bg;
  color: $color-text-base;
}

.body-wrapper {
  @apply w-full flex justify-center;
}

.content-wrapper {
  width: 100%;
  display: flex;
  align-items: flex-end;
}

.settings-button {
  width: 2.5em;
  height: 2.5em;
  border-radius: $search-bar-height;
  background-color: $color-text-base;
  color: $color-bg-light;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  position: fixed;
  right: 0;
  margin-top:  10px;
  margin-right:10px;
  z-index: 3;
}

.settings-icon {
  transition: transform 100ms ease-in-out;
}

.settings-button:hover > .settings-icon {
  transform: rotate(30deg);
}

.settings-button--active > .settings-icon {
  transform: rotate(30deg);
}

.main-container {
  width: $main-container-size;
  height: 100%;
  background-color: $color-bg;
  padding: $base-padding;
}

.settings-container{
  margin-top:$main-container-margin-top
}

.main-container__response {
  padding: $half-padding !important;
}

.you-searched {
  font-size: 10px;
  font-style: italic;
  opacity: 75%;
}

.search-query {
  font-size: 14px;
}

.mathmex-response {
  background-color: $color-bg-light;
  border-radius: $base-padding;
  padding: $base-padding;
  position: relative;
}

.mathmex-response__mobile {
  background-color: $color-bg-light;
  border-radius: $base-padding;
  padding: $base-padding $base-padding 0px $base-padding;
  position: relative;
}

.mathmex-response--title {
  font-size: 24px;
  font-weight: bold;
  width: 90%;
  padding-top: 12px;
}

.mathmex-response--relevance {
  cursor: pointer;
  user-select: none;
  text-transform: uppercase;
  background-color: $color-text-base;
  font-size: 12px;
  width: 96px;
  text-align: center;
  font-weight: bold;
  display: inline-block;
  padding: $half-padding $half-padding;
  border-radius: 0 $base-padding 0 $half-padding;
  position: absolute;
  top: 0;
  right: 0;
}

.mathmex-response--relevance__high {
  @apply text-green-400;
}

.mathmex-response--relevance__medium {
  @apply text-orange-400;
}

.mathmex-response--relevance__low {
  @apply text-red-400;
}

.mathmex-response--link {
  cursor: pointer;
  user-select: none;
  text-decoration: underline;
  color: #862286;
  font-size: 12px;
  display: inline-block;
  max-width: 100%;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}

.mathmex-response--text {
  text-align: justify;
  margin: 48px 64px 24px 64px;
  position: relative;
}

.mathmex-response--image-container{
  display: flex;
  justify-content: center;
  align-items: center;
}

.mathmex-response--text__mobile {
  text-align: justify;
  margin: 48px 0px 12px 0px;
  position: relative;
}

.mathmex-response--quote-left {
  position: absolute;
  font-size: 48px;
  opacity: 20%;
  top: -24px;
  left: -64px;
}

.mathmex-response--quote-right {
  position: absolute;
  font-size: 48px;
  opacity: 20%;
  bottom: -24px;
  right: -64px;
}

.mathmex-response--thumbsup {
  @apply mb-4 opacity-50 hover:text-green-500 cursor-pointer;
}

.mathmex-response--thumbsup__active {
  @apply opacity-75 text-green-500;
}

.mathmex-response--thumbsdown {
  @apply opacity-50 hover:text-red-500 cursor-pointer;
}

.mathmex-response--thumbsdown__active {
  @apply opacity-75 text-red-500;
}

.query-block-wrapper {
  @apply w-full;
}

.horizontal-rule {
  @apply w-full flex items-center justify-center border-b border-black border-opacity-10;
}

.next-result {
  @apply h-10 my-2 px-2 flex justify-center items-center bg-black bg-opacity-0 hover:bg-opacity-10 rounded-md text-sm cursor-pointer select-none;
}

.side-container {
  width: $side-container-size;
  height: 100%;
  padding: $base-padding;
  visibility: hidden;
  opacity: 0;
  transition: visibility 0s, opacity 100ms ease-in-out;
}

.side-container__search {
  width: 25% !important;
  text-align: right;
  opacity: 1;
  visibility: visible;
}

.main_container__search {
  height: 75% !important;
}

.content-wrapper:hover > .side-container {
  visibility: visible;
  opacity: 1;

  transition: visibility 200ms, opacity 100ms ease-in-out;
}

.header-bar {
  // position:fixed
  @apply max-w-screen-lg flex justify-center /*fixed*/;
  width: $center-container-width;
  background-color: $color-bg;
  z-index: 2;
  flex-direction: column
}

.centered-wrapper {
  @apply w-full max-w-screen-lg flex flex-col;
}

.response-container {
  width: $center-container-width;
  // margin-top: $header-height;
  margin-bottom: $footer-height;
  padding-top: $base-padding;
}

.bottom-bar {
  @apply w-full max-w-screen-xl fixed bottom-0 flex items-center justify-center;
  background-color: $color-bg;
  height: $footer-height;
  padding: $base-padding;
  font-weight: 500;
}

.footer-option {
  font-size: $footer-font-size;
  margin-left: $footer-margin;
  margin-right: $footer-margin;
  cursor: pointer;
  color: $color-light-text;
}

.footer-option:hover {
  color: rgb(60, 60, 60);
}

.footer-copyright {
  font-size: $footer-font-size;
  margin-left: $footer-margin;
  margin-right: $footer-margin;
  color: $color-text-base;
}

.search-bar {
  height: $search-bar-height;
  width: $main-container-size;
  bottom: 0;
  left: 0;
  padding: 0 $base-padding $base-padding;
}

.sources-dropdown {
  position: absolute;
  height: $search-bar-height;
  width: $side-container-size;
  right: 0;
  bottom: 0;
  background-color: $color-bg;
  padding: 0 0 $base-padding;
  display: flex;
  justify-content: center;
  align-items: center;
}

.logo-container {
  width: 100%;
  // This was the initial height but my 118px will need a fix
  // height: calc($header-height - $search-bar-height);
  height: 118px;
  top: 0;
  left: 0;
  padding: $base-padding;
  display: flex;
  justify-content: center;
}

.mathmex-logo {
  background-image: url("../public/banner.png");
  width: 500px;
  // height: 50%;
  background-size: contain;
  background-repeat: no-repeat;
  background-position: center;
}

.search-button {
  width: 96px;
  height: 100%;
  background-color: $color-text-base;
  color: white;
  border-radius: 50vh 0px 0px 50vh;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
}

.math-input-full {
  height: 100%;
  width: 100%;
  border-radius: 50vh;
  background-color: white;
  display: flex;
  justify-content: center;
  align-items: center;
}

math-field {
  width: 100%;
  border-radius: 50vh;
  display: block;
  margin-left: 16px;
}

math-field:focus-within {
  outline: none;
}

.data-source {
  @apply relative w-40 h-52 p-4 rounded-md cursor-pointer hover:shadow-sm hover:scale-105 transform transition-all flex-shrink-0;
  background-color: rgba($color-text-base, 0.05);
}

.data-source--inactive:hover {
  @apply bg-opacity-5;
}

.data-source--active {
  @apply bg-opacity-100;
  background-color: $color-bg-light;
}

.data-source__check {
  @apply absolute bottom-4 right-4 opacity-0 transition-all;
}

.data-source--inactive:hover > .data-source__check {
  @apply opacity-50;
}

.data-source--active > .data-source__check {
  @apply opacity-100 text-amber-500;
}

/* scrollbar */
::-webkit-scrollbar {
  width: 5px;
  height: 5px;
}

::-webkit-scrollbar-track {
  -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
  -webkit-border-radius: 10px;
  border-radius: 10px;
}

::-webkit-scrollbar-thumb {
  -webkit-border-radius: 10px;
  border-radius: 10px;
  background: rgba(255, 255, 255, 0.3);
  -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.5);
}

::-webkit-scrollbar-thumb:window-inactive {
  background: rgba(255, 255, 255, 0.3);
}
//----------------------------------------------------------------------

.mexinput-container{
  max-width: 1024px;
  width: 1024px;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  background-color: white;
  border-color:#edebf6;
  border-width: 3px;
}

.mexinput-container:focus-within{
  border-color:#231C23 !important;
  border-width: 3px;
}

.multi-mathfield-container{
  margin-top: 20px;
  width: 100%;
  max-width: 1024px;
  height: 15vh;
  overflow-y: auto;
}

.mex-textarea-button-container{
  position: absolute;
  bottom: 15px;
  right: 10px;
}

.mex-textarea{
  word-wrap: break-word;
	overflow-wrap: break-word;
	white-space: normal;	
  outline: 0px solid transparent;
}

[data-placeholder]:empty:before{
  content: attr(data-placeholder);
  color: #888;
  font-style: italic;
}



.mex-textarea-button{
  margin-left: 15px;
  cursor: pointer;
}





.equation-symbol{
  cursor: pointer;
  padding:1px;
  border-radius:3px;
  font-family:'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
  border-width: 1px;
  border-color:black;
  background-color:grey;
  color:white
}


.mf-container{
  padding-bottom: 10px;
}

.mex-input-wrapper{
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 100%;
}

.response-container-wrapper{
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
}

.mathmex-response__failure{
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 16px;
  border-color: red;
  border-width:$mexinput-border-width ;
  height: $queryblock-height;
}

.mathmex-response__failure--content{
  font-size: 3.35rem;
}


.mathmex-response__failure--retry{
  -webkit-transition: -webkit-transform 0.5s ease-in-out;
  transition: transform 0.5s ease-in-out;
  cursor: pointer;
}
.mathmex-response__failure--retry:hover{
  -webkit-transition: -webkit-transform 0.5s ease-in-out;
  transition: transform .5s ease-in-out;
  transform: rotate(90deg);
}

.mathmex-response__loading{
  border-radius: 16px;
  @apply shimmer
}

.shimmer{
  @keyframes placeholder-shimmer {
    0%   { background-position:  100%; }
    100% { background-position: -100%; }
  }
    animation: 3s cubic-bezier(.25,.50,.75,.50) infinite placeholder-shimmer;
    background-image: linear-gradient(to right, 
      rgba(0,0,0,0.00)   0%, 
      rgba(0,0,0,0.25)  25%, 
      rgba(0,0,0,0.00)  50%, 
      rgba(0,0,0,0.25)  75%, 
      rgba(0,0,0,0.00) 100%
    );
    background-size: 200%;
    height: $queryblock-height; width: 100%;
}

//---------------------------ADAPTIVE STYLES----------------------  

// Mobile Devices
@media only screen and (max-width: 640px) {
   $adaptive-footer-font-size:12px;
  .logo-container{
    height: 100px;
  }

  .mex-textarea-trash:active{
    color: red;
  }
  .footer-option {
    font-size: $adaptive-footer-font-size;
  }
  
  .footer-copyright {
    font-size: $adaptive-footer-font-size;
  }
}

// Very Small Screens
@media only screen and (max-width: 440px) {
  $adaptive-footer-font-size:9px;
  .mathmex-response__failure--content {
    font-size: 2rem;
  }
 .footer-option {
   font-size: $adaptive-footer-font-size;
  }
 
 .footer-copyright {
   font-size: $adaptive-footer-font-size;
  }
}
// Desktop
@media only screen and (min-width: 640px) {
  .mex-textarea-trash:hover{
    color: red;
  }
}